<template><section><h1>KmlDataSource</h1>
<p>The <code v-pre>vc-datasource-kml</code> component is used to load a dataSource which processes Keyhole Markup Language 2.2 (KML).KML support in Cesium is incomplete, but a large amount of the standard, as well as Google's gx extension namespace, is supported. See Github issue #873 for a detailed list of what is and isn't support. Cesium will also write information to the console when it encounters most unsupported features.</p>
<p>Non visual feature data, such as atom:author and ExtendedData is exposed via an instance of KmlFeatureData, which is added to each Entity under the kml property.</p>
<h2>Example</h2>
<h3>Load a KmlDataSource with <code v-pre>vc-datasource-kml</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-datasource-kml data="./statics/SampleData/kml/gdpPerCapita2008.kmz" :show="show"></vc-datasource-kml>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
